.products-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
 }
 h2 {
    margin: 0;
    text-align: center;
    color: #ffffff;
    font-family: 'Exo 2', sans-serif;
    font-size: 36px;
    line-height: 46px;
    font-weight: 400;
    text-shadow: 0 1px 0 #000000;
  }
  @media (max-width: 768px) {
    h2 {
      font-size: 30px;
      line-height: 36px;
  
    } 
  }