@import url(https://fonts.googleapis.com/css?family=Exo+2:100&subset=cyrillic);
*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0e0e0e; }

.App {
    width: 100vw;
    min-height: 100vh;
    min-width: 360px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.5)), color-stop(50%, rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, 0.5))), url(data:image/jpeg;base64,);
    background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%), url(data:image/jpeg;base64,);
    background: linear-gradient(to top, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.5) 100%), url(data:image/jpeg;base64,);  
}
.container {
    width: 100%;
    max-width: 1200px;
  }
  
.products-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
 }
 h2 {
    margin: 0;
    text-align: center;
    color: #ffffff;
    font-family: 'Exo 2', sans-serif;
    font-size: 36px;
    line-height: 46px;
    font-weight: 400;
    text-shadow: 0 1px 0 #000000;
  }
  @media (max-width: 768px) {
    h2 {
      font-size: 30px;
      line-height: 36px;
  
    } 
  }
.card-wrapper {
  margin: 20px; }

.card {
  width: 320px;
  height: 480px;
  margin-bottom: 15px;
  position: relative;
  border-radius: 12px;
  background: -webkit-linear-gradient(315deg, transparent 30px, #1698d9 0);
  background: linear-gradient(135deg, transparent 30px, #1698d9 0);
  -webkit-transition: all .1s linear;
  transition: all .1s linear;
  cursor: pointer; }
  .card:hover {
    background: -webkit-linear-gradient(315deg, transparent 30px, #2ea8e6 0);
    background: linear-gradient(135deg, transparent 30px, #2ea8e6 0); }
    .card:hover + .card-text .buy-text-button,
    .card:hover + .card-text .text-blue {
      color: #2ea8e6;
      border-color: #2ea8e6; }
    .card:hover .product-weight {
      background-color: #2ea8e6; }
  .card.selected {
    background: -webkit-linear-gradient(315deg, transparent 30px, #d91667 0);
    background: linear-gradient(135deg, transparent 30px, #d91667 0); }
    .card.selected .product-weight {
      background-color: #d91667; }
    .card.selected.hover {
      background: -webkit-linear-gradient(315deg, transparent 30px, #e62e7a 0);
      background: linear-gradient(135deg, transparent 30px, #e62e7a 0); }
      .card.selected.hover .product-weight {
        background-color: #e62e7a; }
  .card.disabled {
    background: -webkit-linear-gradient(315deg, transparent 30px, #b3b3b3 0);
    background: linear-gradient(135deg, transparent 30px, #b3b3b3 0);
    cursor: default; }
    .card.disabled p,
    .card.disabled .product-title {
      color: #b3b3b3; }
    .card.disabled .product-image {
      opacity: 0.5; }
    .card.disabled .product-weight {
      background-color: #b3b3b3; }

.card-body {
  position: absolute;
  left: 4px;
  right: 4px;
  bottom: 4px;
  top: 4px;
  padding: 20px 44px;
  background: -webkit-linear-gradient(315deg, transparent 28px, #f2f2f2 0);
  background: linear-gradient(135deg, transparent 28px, #f2f2f2 0);
  background-position: left bottom;
  background-repeat: no-repeat;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
  font-family: "Trebuchet MS"; }

p {
  color: #666666;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  margin: 0; }

.product-topline {
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 10px; }

.product-title {
  color: #000000;
  font-size: 48px;
  line-height: 48px;
  font-weight: 700;
  margin: 0 0 18px -2px; }
  .product-title span {
    display: block;
    font-size: 24px;
    line-height: 24px;
    margin-left: 2px;
    margin-top: 3px; }

.product-image {
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom-left-radius: 8px; }

.product-weight {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 80px;
  border-radius: 50%;
  background-color: #1698d9;
  position: absolute;
  bottom: 11px;
  right: 11px;
  color: #ffffff;
  font-size: 42px;
  line-height: 40px;
  -webkit-transition: all .1s ease;
  transition: all .1s ease; }
  .product-weight span {
    display: block;
    font-size: 20px;
    line-height: 20px; }

.card-text {
  height: 16px;
  font-family: "Trebuchet MS";
  text-shadow: 0px 1px 0 #000000;
  font-size: 13px;
  line-height: 1.23;
  text-align: center;
  color: #ffffff; }

.buy-text-button {
  display: inline-block;
  color: #1698d9;
  font-weight: 600;
  cursor: pointer;
  border-bottom: 1px dashed #1698d9; }
  .buy-text-button:hover {
    color: #2ea8e6;
    border-bottom: 1px dashed #2ea8e6; }

.text-bold {
  font-weight: 700; }

.text-rose {
  color: #e62e7a; }

.text-blue {
  color: #1698d9; }

.text-yellow {
  color: #ffff66; }

