$blue-default: #1698d9;
$blue-hover: #2ea8e6;
$rose-default: #d91667;
$rose-hover: #e62e7a;
$grey-disabled: #b3b3b3;

.card-wrapper {
    margin: 20px;
}

.card {
    width: 320px;
    height: 480px;
    margin-bottom: 15px;
    position: relative;
    border-radius: 12px;
    background: linear-gradient(135deg, transparent 30px, $blue-default 0);
    transition: all .1s linear;
    cursor: pointer;

    &:hover {
        background: linear-gradient(135deg, transparent 30px, $blue-hover 0);

        &+.card-text {
            & .buy-text-button,
            .text-blue {
                color: $blue-hover;
                border-color: $blue-hover;
            }
        }

        & .product-weight {
            background-color: $blue-hover;
        }
    }

    &.selected {
        background: linear-gradient(135deg, transparent 30px, $rose-default 0);

        & .product-weight {
            background-color: $rose-default;
        }
        
        &.hover {
            background: linear-gradient(135deg, transparent 30px, $rose-hover 0);

            .product-weight {
                background-color: $rose-hover;
            }
        }
    }

    &.disabled {
        background: linear-gradient(135deg, transparent 30px, $grey-disabled 0);
        cursor: default;

        & p,
        .product-title {
            color: #b3b3b3;
        }

        & .product-image {
            opacity: 0.5;
        }

        & .product-weight {
            background-color: $grey-disabled;
        }
    }
}

.card-body {
    position: absolute;
    left: 4px;
    right: 4px;
    bottom: 4px;
    top: 4px;
    padding: 20px 44px;
    background: linear-gradient(135deg, transparent 28px, #f2f2f2 0);
    background-position: left bottom;
    background-repeat: no-repeat;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top-right-radius: 8px;
    font-family: "Trebuchet MS";
}

p {
    color: #666666;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    margin: 0;
}

.product-topline {
    font-size: 16px;
    line-height: 16px;
    margin-bottom: 10px;
}

.product-title {
    color: #000000;
    font-size: 48px;
    line-height: 48px;
    font-weight: 700;
    margin: 0 0 18px -2px;

    & span {
        display: block;
        font-size: 24px;
        line-height: 24px;
        margin-left: 2px;
        margin-top: 3px;
    }
}

.product-image {
    position: absolute;
    bottom: 0;
    left: 0;
    border-bottom-left-radius: 8px;
}

.product-weight {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 80px;
    border-radius: 50%;
    background-color: $blue-default;
    position: absolute;
    bottom: 11px;
    right: 11px;
    color: #ffffff;
    font-size: 42px;
    line-height: 40px;
    transition: all .1s ease;

    & span {
        display: block;
        font-size: 20px;
        line-height: 20px;
    }
}

.card-text {
    height: 16px;
    font-family: "Trebuchet MS";
    text-shadow: 0px 1px 0 #000000;
    font-size: 13px;
    line-height: 1.23;
    text-align: center;
    color: #ffffff;
}

.buy-text-button {
    display: inline-block;
    color: $blue-default;
    font-weight: 600;
    cursor: pointer;
    border-bottom: 1px dashed $blue-default;
    &:hover {
        color: $blue-hover;
        border-bottom: 1px dashed $blue-hover;
    }
}

.text-bold {
    font-weight: 700;
}

.text-rose {
    color: $rose-hover;
}

.text-blue {
    color: $blue-default;
}

.text-yellow {
    color: #ffff66;
}